<!--公共头部-->
<template>
    <div class="headArea">
        <div style="width:150px;text-align: center;flex:0 0 auto;">长运公交集团</div>
        <el-menu class="el-menu-demo" mode="horizontal" style="flex:0 0 auto;" active-text-color="#62b4ff"
                 default-active="0">
            <el-menu-item v-for="(menu,index) in headData" :index="String(index)" style="padding: 0 10px"
                          @click="setChildMenu(menu.child,menu.path)">{{menu.name}}
            </el-menu-item>
        </el-menu>
        <div style="width:100px;text-align: center;flex:0 0 auto;">LYQ</div>
    </div>
</template>

<script>

    export default {
        name: "Header",
        components: {},

        data() {
            return {
                username: "LYQ",
                isFocus: "",
                headData: [
                    {
                        name: "人事管理",
                        path: "/personalInfo",
                        child: [
                            {
                                name: "人员管理", path: "/personalInfo", icon: "personManage",
                                child: [
                                    {
                                        name: "人员档案", path: "/personalInfo", icon: "personFiles"
                                    },
                                    {
                                        name: "人员合同", path: '/personalContract', icon: 'contract'
                                    },
                                    {
                                        name: "人员离职", path: '/personalDimission', icon: 'dimission'
                                    }

                                ]
                            },
                            {
                                name:"系统配置",path: "/personalInfo", icon: "sysManage",
                                child:[
                                    {
                                        name:"组织管理",path: "/personalInfo", icon: "organization",
                                    }
                                ]

                            }
                        ]
                    },

                    {
                        name: "车辆管理",
                        path: "/carInfo",
                        icon: 'vehicleManage',
                        child: [
                            {
                                name: '车辆管理', path: "/carInfo",icon: 'vehicleManage',
                                child: [
                                    {name: "车辆档案", icon: "vehicleFiles", path: "/carInfo"},
                                    {name: "车辆报废", icon: "scraped", path: "/carScraped"},
                                    {name: "车辆年审", icon: "examine", path: "/carExamine"},
                                ]
                            },
                        ],

                    }
                ]
            }
        },
        created() {
            this.setChildMenu(this.headData[0].child)
        }
        ,
        methods: {
            //向父组件传值
            setChildMenu: function (obj, path) {
                this.$emit("childMenu", obj);
                this.jumpPath(path);
            }
            ,
            // 页面跳转
            jumpPath: function (path) {
                if (path) {
                    this.$router.push({path: path, params: {id: '1111'}})
                }
            }

        }
    }
</script>

<style>
    .headArea {
        width: 100%;
        height: 61px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: solid 1px #e6e6e6;
    }

    .el-menu.el-menu--horizontal {
        border-bottom: none;

    }
</style>



