<template>
    <div class="width:100px;height:100px">
<!--        <iSymbol iconName="suo"></iSymbol>-->
        <Header class="headArea" v-on:childMenu="childMenu"></Header>
        <AsideLeft :secData="secData"></AsideLeft>
    </div>
</template>

<script>
    import AsideLeft from "./asideLeft"
    import Header from "./header"
    export default {
        name: "full",
        data() {
            return {
                secData: '111',
            };
        },
        components: {
            AsideLeft: AsideLeft,
            Header: Header,
        },
        created(){
        },

        methods:{
            childMenu:function(val) {
                this.secData=val;
            }
        }

    }
</script>
<style>
</style>



