<!--公共左侧菜单最多三级菜单-->
<template>
    <div class="asideArea">
        <el-menu
                class="el-menu-vertical-demo"
                :unique-opened="true"
                default-active="0-0"
                :collapse-transition="false"
                :key="refreshNum"
        >
            <div v-for="(menu,index) in secData">
                <!--               第1级存在2级子菜单-->
                <el-submenu v-if="menu.child" :index="String(index)">
                    <template slot="title">
                        <iSymbol :iconName="String(menu.icon)" style="display: inline-block"></iSymbol>
                        <span>{{menu.name}}</span>
                    </template>

                    <div v-for="(secMenu,secIndex) in menu.child">
                        <!--               第2级存在3级子菜单-->
                        <el-submenu v-if="secMenu.child" :index="index+'-'+secIndex">
                            <template slot="title">{{secMenu.name}}</template>
                            <el-menu-item
                                    v-for="(thiMenu,thiIndex) in secMenu.child"
                                    @click="jumpPath(thiMenu.path)"
                                    :key="thiMenu.path"
                                    :index="index+'-'+secIndex+'-'+thiIndex"
                            >{{thiMenu.name}}
                            </el-menu-item>
                        </el-submenu>
                        <!--               第2级不存在3级子菜单-->
                        <el-menu-item-group v-else>
                            <template slot="title"></template>
                            <el-menu-item
                                    :index="index+'-'+secIndex"
                                    @click="jumpPath(secMenu.path)"
                                    :key="secMenu.path"
                            >
                                <iSymbol :iconName="String(secMenu.icon)" style="display: inline-block"></iSymbol>
                                <span v-if="!secMenu.child">{{secMenu.name}}</span>
                            </el-menu-item>
                        </el-menu-item-group>
                    </div>
                </el-submenu>
                <!--               第1级不存在2级子菜单-->
                <el-menu-item v-else :index="String(index)" @click="jumpPath(menu.path)" :key="menu.path">
                    <iSymbol :iconName="String(menu.icon)" style="display: inline-block"></iSymbol>
                    <span slot="title">{{menu.name}}</span>
                </el-menu-item>
            </div>
        </el-menu>
        <keep-alive>
            <router-view style="flex:1 1 auto"></router-view>
        </keep-alive>
    </div>
</template>
<script>
    export default {
        name: "AsideLeft",
        props: ["secData", "defaultShow"],
        data() {
            return {
                isCollapse: true,
                refreshNum: 0
            };
        },
        created() {
            // this.$store.commit("setUser",{name:"wang"})
            // console.log(this.$store._actions.setUser("往"));
            this.$store.dispatch("setUser", {name: "me"});
        },
        mounted() {
            console.log(this.$store.getters.user.name);

            this.$store.dispatch("setUser", {name: "he"});
            console.log(this.$store.getters.user.name);
            this.runAsync().then(function (res) {
                console.log(res)
            }).catch(function (res) {
                console.log(res)
            })


        },
        watch: {
            secData(val) {
                this.refreshNum += 1;
            }
        },
        methods: {
            jumpPath: function (path) {
                if (path) {
                    this.$router.push({path: path, params: {id: "1111"}});
                }
            },
            runAsync: function () {
                let p = new Promise(function (resolve, reject) {
                    let timeOut = Math.random() * 2;
                    if(timeOut>1){
                        setTimeout(function () {
                            console.log('执行完成');
                            resolve("执行成功");
                        }, 1000);
                    }else{
                        setTimeout(function () {
                            console.log('执行失败');
                            reject('执行错误');
                        }, 1000);
                    }

                });
                return p;
            },
        }
    };
</script>
<style lang="scss">
    .asideArea {
        display: flex;

        .el-menu-vertical-demo:not(.el-menu--collapse) {
            width: 200px;
            /*min-height: 200px;*/
            min-height: calc(100vh - 62px);
            margin: 0;
            padding: 0;
        }

        .el-menu-item-group__title {
            padding: 0;
            color: #62b4ff;
        }
    }
</style>




